'use strict';

var _themeAddresses = require('@shopify/theme-addresses');

var selectors = {
  addressContainer: '[data-address]',
  addressToggle: '[data-address-toggle]',
  addressCountry: '[data-address-country]',
  addressProvince: '[data-address-province]',
  addressProvinceWrapper: '[data-address-province-wrapper]',
  addressForm: '[data-address-form]',
  addressDeleteForm: '[data-address-delete-form]'
}; /**
    * Customer Addresses Script
    * ------------------------------------------------------------------------------
    * A file that contains scripts highly couple code to the Customer Addresses
    * template.
    *
    * @namespace customerAddresses
    */

var hideClass = 'hide';

function initializeAddressForm(countryProvinceSelector, container) {
  //const countrySelector = container.querySelector(selectors.addressCountry);
  var provinceSelector = container.querySelector(selectors.addressProvince);
  var provinceWrapper = container.querySelector(selectors.addressProvinceWrapper);
  var addressForm = container.querySelector(selectors.addressForm);
  var deleteForm = container.querySelector(selectors.addressDeleteForm);

  // countryProvinceSelector.build(countrySelector, provinceSelector, {
  //  onCountryChange: (provinces) => provinceWrapper.classList.toggle(hideClass, !provinces.length),
  // });

  container.querySelectorAll(selectors.addressToggle).forEach(function (button) {
    button.addEventListener('click', function () {
      addressForm.classList.toggle(hideClass);
    });
  });

  if (deleteForm) {
    deleteForm.addEventListener('submit', function (event) {
      //const confirmMessage = deleteForm.getAttribute('data-confirm-message');

      if (!window.confirm(confirmMessage || 'Are you sure you wish to delete this address?')) {
        event.preventDefault();
      }
    });
  }
}

var addresses = document.querySelectorAll(selectors.addressContainer);

if (addresses.length) {

  var countryProvinceSelector = new _themeAddresses.CountryProvinceSelector(window.theme.allCountryOptionTags);

  addresses.forEach(function (addressContainer) {
    initializeAddressForm(countryProvinceSelector, addressContainer);
  });

  var address = getUrlParameter('address');
  if (address) {
    $('.AddressForm.new').hide();
    $('.AddressForm#' + address).removeClass('hide');
    $('.AccountTitle h1').text('Edit Address');
  }
}

function getUrlParameter(sParam) {
  var sPageURL = window.location.search.substring(1),
      sURLVariables = sPageURL.split('&'),
      sParameterName,
      i;

  for (i = 0; i < sURLVariables.length; i++) {
    sParameterName = sURLVariables[i].split('=');

    if (sParameterName[0] === sParam) {
      return sParameterName[1] === undefined ? true : decodeURIComponent(sParameterName[1]);
    }
  }
};